* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.transit {
  margin-bottom: 20px;
  padding: 20px;
}

.transit-link {
  max-width: 240px;
  color: #fafafa;
  background-color: green;
  border-radius: 20px;
  justify-content: space-between;
  padding: 10px 25px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 67, 0, .7);
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.pokemon-list {
  border-bottom: 5px double #daa520;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 20px 50px;
  list-style: none;
  display: flex;
}

.pokemon-list li {
  min-width: 220px;
  text-align: center;
  color: #fff;
  background-color: rgba(230, 56, 25, .75);
  border-radius: 5px;
  padding-top: 10px;
  position: relative;
}

.pokemon-list li h2 {
  margin: 0 auto;
}

.pokemon-list li h2:first-letter {
  color: #212121;
  text-transform: uppercase;
  font-size: 26px;
}

.pokemon-list li p {
  cursor: pointer;
  background-color: rgba(208, 54, 27, .45);
  border-radius: 35px;
  padding: 4px 25px;
  display: inline-block;
  box-shadow: 0 0 15px rgba(230, 56, 25, .7);
}

.pokemon-list li a {
  color: #fafafa;
  font-size: 16px;
  text-decoration: none;
}

.pokemon-card {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  max-width: 200px;
  border: 1px solid #212121;
  border-radius: 5px;
  padding: 10px;
  overflow: hidden;
}

.card-img-top img {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 5px;
  display: block;
  box-shadow: 0 5px 15px rgba(33, 33, 33, .15);
}

